<template>
    <div class="m-products-list">
            <ul>
              <li
                :class="{'s-nav-active' : item.active}"
                v-for="(item,index) in nav"
                :key="index"
              >{{item.name}}</li>
            </ul>
           <el-row>
               <m-item v-for="(item,index) in productlist" :key="index" :list="item" />
           </el-row>
            
            
          </div>
</template>
<script>
import MItem from "@/components/products/item";
import Api from '@/api/index.js'
const api = new Api()
export default {
    components:{
      MItem,
    },
    created(){

      // console.log(api)
       api.getGoodsList().then((res)=>{
          
            this.productlist = res.data.data
       })
    },
     data() {
    return {
      nav: [
        {
          key: "s-default",
          name: "智能排序",
          active: true
        },
        {
          key: "s-default",
          name: "价格最低",
          active: false
        },
        {
          key: "s-default",
          name: "人气最高",
          active: false
        },
        {
          key: "s-default",
          name: "评价最高",
          active: false
        }
      ],
      productlist: []
    };
  }
}
</script>
<style lang='scss'>
@import "@/assets/css/products/list.scss";
</style>


