<template>

  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item>{{province}}美团</el-breadcrumb-item>
    <el-breadcrumb-item>{{province}}{{$route.params.id}}</el-breadcrumb-item>
  </el-breadcrumb>

</template>
<script>

import {mapState} from 'vuex'
export default {
    data(){
       return{
       }
    },
    computed:{
        ...mapState(['province'])
    },

};
</script>



