<template>
 
    <dl class="s-item">
      <dt>
        <img :src="list.image" alt="tupian">
      </dt>
      <dd>
        <!-- 第一 -->
        <h3>
          <router-link tag="h3" to="/">{{list.title}}</router-link>
        </h3>
        <!-- 第二 -->
        <p>
          <el-rate v-model="list.score" disabled text-color="#ff9900" score-template="评分"></el-rate>

          <span v-if="list.score > 4.5">很好</span>
          <span v-else-if="list.score > 4.0">好</span>
          <span v-else-if="list.score > 3.5">一般</span>
          <span v-else>不错</span>
          <span class="s-item-price">{{list.score}}分</span>
          <span calss="s-item-comment-total">{{list.commentNum}}人评论</span>
        </p>
        <!-- 第三 -->
        <p>
          <span class="item-comment-tab">
            <span
              v-for="(item,i) in list.tab"
              :key="i"
            >{{item + (i < list.tab.length - 1 ? "|" : "")}}</span>
          </span>
          <span>{{list.address}}</span>
          <span class="item-comment-addr">
            <a href="#">
              <i class="el-icon-location"></i>
              <span>查看地图</span>
            </a>
          </span>
        </p>
        <!-- 人均 -->
        <p>
          <span class="s-item-price">¥{{list.avgPrice}}/人起</span>
        </p>

        <ul class="deal-items">

            <li class="items" v-for="(item,index) in list.dealItems" :key="index">
                <p class="deal-title">{{item.title}}</p>
                <span class="deal-price">¥{{item.price}}</span>
                <span class="deal-old-price">门市价¥{{item.counterPrice}}{{item.priceType}}</span>
                <span class="deal-sales">已售{{item.saleNum}}</span>
            </li>
        </ul>
      </dd>
    </dl>
</template>


<script>
export default {
  props: ["list"],
  created(){
    // console.log(this.list)
}
}


</script>




