<template>
    <div class="m-product-select">
        <dl class="tab">
            <dt class="select-title">{{subName}}
                <i class="el-icon-caret-bottom"></i>
            </dt>
            
            <dd>
                <h3>{{subName}}</h3>
                <span v-for="(item,index) in list" :key="index">{{item.name}}</span>
            </dd>
        </dl>
    </div>
</template>
<script>
export default {
     props: ['subName', 'list'],
}
</script>



